vm-checkbox {
  display: flex;
  position: relative;
  padding-left: 23px;
}
vm-checkbox .vm-checkbox--input {
  position: absolute;
  left: 0;
  top: 3px;
  margin: 0 !important;
  opacity: 0;
}
vm-checkbox .vm-checkbox--input:checked + .vm-checkbox--label::before {
  background-color: #20a281;
  border-color: #20a281;
}
vm-checkbox .vm-checkbox--input:checked + .vm-checkbox--label::after {
  content: "\f00c";
  color: #fff;
  font-family: "Font Awesome 5 Pro";
  font-weight: 400;
}
vm-checkbox .vm-checkbox--input:checked:disabled + .vm-checkbox--label::before {
  background-color: #a6b0bc;
  border-color: #a6b0bc;
}
vm-checkbox .vm-checkbox--input:disabled + .vm-checkbox--label {
  cursor: not-allowed;
  opacity: 0.4;
}
vm-checkbox .vm-checkbox--input:focus:not(:checked) + .vm-checkbox--label::before, vm-checkbox .vm-checkbox--input:hover:not(:checked) + .vm-checkbox--label::before {
  background-color: #fafbfc;
}
vm-checkbox .vm-checkbox--label {
  min-height: 20px;
  display: inline-block;
  position: relative;
  cursor: pointer;
  font-weight: 400;
  margin-bottom: 0;
}
vm-checkbox .vm-checkbox--label::before {
  content: "";
  height: 13px;
  width: 13px;
  display: inline-block;
  position: absolute;
  left: -23px;
  top: 3px;
  background-color: #fff;
  border: solid 1px #c1c7d0;
  border-radius: 3px;
  transition: border 0.15s ease-in-out, color 0.15s ease-in-out;
}
vm-checkbox .vm-checkbox--label::after {
  height: 12px;
  width: 12px;
  display: inline-block;
  position: absolute;
  left: -22px;
  top: 2px;
  font-size: 11px;
}