vm-alert {
  width: 100%;
  display: flex;
  align-items: center;
  column-gap: 7px;
  border: solid 1px;
  border-radius: 5px;
  font-size: 13px;
  line-height: 20px;
  padding: 5px 15px;
}
vm-alert.vm-alert__danger {
  background-color: #fde1e0;
  border-color: #f0d5d3;
  color: #721c24;
}
vm-alert.vm-alert__hidden {
  display: none;
}
vm-alert.vm-alert__info {
  background-color: #dcecfc;
  border-color: #cbe4fb;
  color: #172b4a;
}
vm-alert.vm-alert__success {
  background-color: #d4edda;
  border-color: #d2e6ce;
  color: #105242;
}
vm-alert.vm-alert__warning {
  background-color: #fff3cd;
  border-color: #e2d7c3;
  color: #533f03;
}
vm-alert.vm-alert__lg {
  font-size: 16px;
  padding: 15px 20px;
}
vm-alert .vm-alert--icon {
  font-size: 16px;
}
vm-alert .vm-alert--text {
  flex: 1;
}