.vm-btns-demo {
  border: solid 1px #c1c7d0;
}
.vm-btns-demo td,
.vm-btns-demo th {
  padding: 8px;
  text-align: center;
}
.vm-btns-demo th {
  background-color: #eff2f7;
  border-bottom: solid 1px #c1c7d0;
  border-right: solid 1px #c1c7d0;
}
.vm-btns-demo tr:not(:last-of-type) td {
  border-bottom: 1px solid #e7eaec;
}