.vm-toggle {
  height: 24px;
  width: 48px;
  position: relative;
  display: inline-block;
  margin-bottom: 0;
}
.vm-toggle input {
  height: 0;
  width: 0;
  display: none;
}
.vm-toggle input:checked + .vm-toggle--slider:before {
  transform: translateX(24px);
}
.vm-toggle .vm-toggle--slider {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  top: 0;
  background-color: #c1c7d0;
  border-radius: 50px;
  cursor: pointer;
  transition: 0.2s;
}
.vm-toggle .vm-toggle--slider:before {
  height: 18px;
  width: 18px;
  position: absolute;
  bottom: 3px;
  left: 3px;
  background-color: #fff;
  border-radius: 50%;
  content: "";
  transition: 0.2s;
}
.vm-toggle.vm-toggle__disabled .vm-toggle--slider {
  cursor: not-allowed;
}
.vm-toggle.vm-toggle__disabled:not(.vm-toggle__toggled) .vm-toggle--slider {
  background-color: #ebecf0;
}
.vm-toggle.vm-toggle__disabled:not(.vm-toggle__toggled) .vm-toggle--slider:before {
  background-color: #c1c7d0;
}
.vm-toggle.vm-toggle__small {
  height: 16px;
  width: 32px;
}
.vm-toggle.vm-toggle__small input:checked + .vm-toggle--slider:before {
  transform: translateX(16px);
}
.vm-toggle.vm-toggle__small .vm-toggle--slider {
  border-radius: 10px;
}
.vm-toggle.vm-toggle__small .vm-toggle--slider:before {
  height: 12px;
  width: 12px;
  bottom: 2px;
  left: 2px;
}
.vm-toggle.vm-toggle__toggled .vm-toggle--slider {
  background-color: #20a281;
}
.vm-toggle.vm-toggle__toggled.vm-toggle__disabled .vm-toggle--slider {
  opacity: 0.65;
}