vm-tabs {
  display: flex;
  flex-wrap: wrap;
}
vm-tabs .vm-tabs--item {
  height: 39px;
  display: flex;
  align-items: center;
  column-gap: 10px;
  font-size: 16px;
  position: relative;
  text-align: center;
}
vm-tabs .vm-tabs--item.vm-tabs--item__active {
  font-weight: 700;
}
vm-tabs .vm-tabs--item:focus, vm-tabs .vm-tabs--item:hover {
  color: #172b4a !important;
  outline: none;
  text-decoration: none;
}

.vm-tabs__primary, vm-tabs[primary] {
  border-bottom: solid 3px #ebecf0;
  column-gap: 10px;
}
.vm-tabs__primary .vm-tabs--item, vm-tabs[primary] .vm-tabs--item {
  color: #a6b0bc;
  padding: 0 20px;
  top: 3px;
}
.vm-tabs__primary .vm-tabs--item.vm-tabs--item__active, vm-tabs[primary] .vm-tabs--item.vm-tabs--item__active {
  border-bottom: solid 3px #20a281;
  color: #172b4a;
}

.vm-tabs__secondary, vm-tabs[secondary] {
  border-bottom: solid 1px #c1c7d0;
  column-gap: 5px;
}
.vm-tabs__secondary .vm-tabs--item, vm-tabs[secondary] .vm-tabs--item {
  border: solid 1px #c1c7d0;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
  padding: 0 25px;
  top: 1px;
}
.vm-tabs__secondary .vm-tabs--item.vm-tabs--item__active, vm-tabs[secondary] .vm-tabs--item.vm-tabs--item__active {
  border-bottom-color: #fff;
  color: #172b4a;
}
.vm-tabs__secondary .vm-tabs--item:not(.vm-tabs--item__active), vm-tabs[secondary] .vm-tabs--item:not(.vm-tabs--item__active) {
  background-color: #eff2f7;
  color: #737f92;
}

.vm-tabs__vertical, vm-tabs[vertical] {
  height: 100%;
  width: 270px;
  display: block;
  border-right: solid 1px #c1c7d0;
}
.vm-tabs__vertical .vm-tabs--item, vm-tabs[vertical] .vm-tabs--item {
  height: 45px;
  width: 100%;
  border: solid 1px #c1c7d0;
  border-right: none;
  color: #172b4a;
  padding: 0 22px;
}
.vm-tabs__vertical .vm-tabs--item:focus, vm-tabs[vertical] .vm-tabs--item:focus, .vm-tabs__vertical .vm-tabs--item:hover, vm-tabs[vertical] .vm-tabs--item:hover {
  background-color: #eee;
}
.vm-tabs__vertical .vm-tabs--item:not(:first-of-type), vm-tabs[vertical] .vm-tabs--item:not(:first-of-type) {
  border-top: none;
}
.vm-tabs__vertical .vm-tabs--item.vm-tabs--item__active, vm-tabs[vertical] .vm-tabs--item.vm-tabs--item__active {
  background-color: #ebf6f3;
  box-shadow: inset 5px 0px 0px #20a281;
}