vm-dialog-duotone .vm-dialog-duotone--header {
  height: 140px;
  position: relative;
  display: flex;
  align-items: center;
  border-top-right-radius: 10px;
  border-top-left-radius: 10px;
}
vm-dialog-duotone .vm-dialog-duotone--header > vm-button {
  position: absolute;
  top: 4px;
  right: 4px;
}
vm-dialog-duotone .vm-dialog-duotone--header > vm-button button {
  color: #a6b0bc;
}
vm-dialog-duotone .vm-dialog-duotone__success {
  background-color: #d4edda;
  color: #105242;
}
vm-dialog-duotone .vm-dialog-duotone__info {
  background-color: #dcecfc;
  color: #172b4a;
}
vm-dialog-duotone .vm-dialog-duotone__warning {
  background-color: #fff3cd;
  color: #533f03;
}
vm-dialog-duotone .vm-dialog-duotone__danger {
  background-color: #fde1e0;
  color: #d61100;
}
vm-dialog-duotone .vm-dialog-duotone--title {
  display: flex;
  width: 100%;
  align-items: center;
  flex-direction: column;
}
vm-dialog-duotone .vm-dialog-duotone--title .vm-dialog-duotone--icon {
  font-size: 2em;
  margin: 16px;
}
vm-dialog-duotone .vm-dialog .vm-dialog--body {
  padding-bottom: 0;
}
vm-dialog-duotone .vm-dialog .vm-dialog--footer {
  border-top: 0;
  justify-content: center;
}