.vm-dialog {
  width: 600px;
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.15);
}
.vm-dialog.vm-dialog__lg {
  width: 900px;
}
.vm-dialog .vm-dialog--body {
  padding: 25px;
}
.vm-dialog .vm-dialog--footer,
.vm-dialog .vm-dialog--header {
  display: flex;
  align-items: center;
  padding: 0 25px;
}
.vm-dialog .vm-dialog--footer {
  height: 75px;
  column-gap: 15px;
  justify-content: flex-end;
  border-top: solid 1px #c1c7d0;
}
.vm-dialog .vm-dialog--header {
  height: 70px;
  justify-content: space-between;
  border-bottom: solid 1px #c1c7d0;
}
.vm-dialog .vm-dialog--header .vm-dialog--title {
  font-size: 18px;
  font-weight: 700;
}

input.vm-input {
  width: 100%;
  border: 1px solid #c1c7d0;
  outline: none;
  height: 40px;
  border-radius: 3px;
  font-size: 16px;
  line-height: 19px;
  padding: 10px 13px;
}
input.vm-input:disabled {
  background-color: #fafbfc;
  cursor: not-allowed;
}
input.vm-input:focus {
  border-color: #20a281;
}
input.vm-input::placeholder {
  color: #a6b0bc;
}

textarea.vm-input {
  width: 100%;
  border: 1px solid #c1c7d0;
  outline: none;
  min-height: 40px;
  max-width: 100%;
  min-width: 100%;
  border-radius: 4px;
  box-shadow: inset 0px 2px 2px rgba(0, 0, 0, 0.08);
  font-size: 14px;
  line-height: 17px;
  padding: 8px;
}
textarea.vm-input:disabled {
  background-color: #fafbfc;
  cursor: not-allowed;
}
textarea.vm-input:focus {
  border-color: #20a281;
}
textarea.vm-input::placeholder {
  color: #999;
}
textarea.vm-input.no-resize {
  resize: none;
}

.vm-input--no-arrows::-webkit-outer-spin-button,
.vm-input--no-arrows::-webkit-inner-spin-button {
  appearance: none;
}

.vm-input-group {
  display: flex;
}
.vm-input-group .vm-input {
  border-radius: 0 3px 3px 0;
}
.vm-input-group .vm-input-group-addon {
  display: flex;
  align-items: center;
  border: 1px solid #c1c7d0;
  border-right: none;
  border-radius: 3px 0 0 3px;
  font-size: 16px;
  padding: 0 13px;
}
.vm-input-group:focus-within .vm-input-group-addon {
  border-color: #20a281;
}

.has-error > .vm-input,
.has-error > .vm-input-group .vm-input,
.has-error > .vm-input-group .vm-input-group-addon {
  border-color: #d61100;
}