vm-pagination {
  width: 100%;
  display: flex;
  justify-content: flex-end;
  padding: 20px 0;
}
vm-pagination .vm-pagination {
  height: 30px;
  display: grid;
  grid-template-columns: 40px 30px 80px 30px 40px;
  align-items: center;
  background-color: #fff;
  font-size: 16px;
  line-height: 24px;
}
vm-pagination .vm-pagination .vm-pagination--btn {
  height: 100%;
  width: 100%;
  background-color: #fff;
  border: solid 1px #c1c7d0;
  border-radius: 0;
  outline: none;
}
vm-pagination .vm-pagination .vm-pagination--btn:first-of-type {
  border-bottom-left-radius: 4px;
  border-top-left-radius: 4px;
}
vm-pagination .vm-pagination .vm-pagination--btn:last-of-type {
  border-bottom-right-radius: 4px;
  border-top-right-radius: 4px;
}
vm-pagination .vm-pagination .vm-pagination--btn:not(:last-of-type) {
  border-right: none;
}
vm-pagination .vm-pagination .vm-pagination--btn:focus, vm-pagination .vm-pagination .vm-pagination--btn:hover {
  background-color: #eff2f7;
  cursor: pointer;
}
vm-pagination .vm-pagination .vm-pagination--btn:disabled {
  background-color: #fafbfc;
  color: rgba(115, 127, 146, 0.4);
  cursor: not-allowed;
}
vm-pagination .vm-pagination .vm-pagination--info {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  border: solid 1px #c1c7d0;
  border-right: none;
}