vm-badge {
  height: 20px;
  width: min-content;
  display: flex;
  align-items: center;
  border-radius: 50px;
  font-size: 12px;
  font-weight: 700;
  line-height: 14px;
  padding: 0 7px 0 6px;
}

vm-badge[danger],
.vm-badge__danger {
  background-color: #d61100;
  color: #fff;
}

vm-badge[primary],
.vm-badge__primary {
  background-color: #6666d2;
  color: #fff;
}

vm-badge[secondary],
.vm-badge__secondary {
  background-color: #ebecf0;
}