vm-label {
  display: inline-block;
  padding: 2px 8px 2px 8px;
  margin: 0 10px 10px 0;
  border-radius: 3px;
  background-color: #c1c7d0;
  color: #172b4a;
}
vm-label:last-child {
  margin-right: 0;
}
vm-label[pill] {
  padding: 0;
  margin: 0;
}
vm-label[pill] vm-button {
  height: 100%;
  width: 100%;
  display: block;
}
vm-label[pill] vm-button button {
  border-radius: 3px;
  padding: 4px 10px 4px 10px;
  height: 100%;
  width: 100%;
  border: 0;
  background-color: transparent;
  font-size: 16px;
}
vm-label[light], vm-label.vm-label--light {
  background-color: #eff2f7;
  color: #172b4a;
}
vm-label[danger] vm-label.vm-label--light {
  background-color: #d61100;
  color: #fff;
}
vm-label[danger][light], vm-label.vm-label--danger.vm-label--light {
  background-color: #fde1e0;
  color: #721c24;
}
vm-label[danger-low], vm-label.vm-label--danger-low {
  background-color: #ed7d54;
  color: #fff;
}
vm-label[warning], vm-label.vm-label--warning {
  background-color: #feb836;
  color: #172b4a;
}
vm-label[warning][light], vm-label.vm-label--warning.vm-label--light {
  background-color: #fff3cd;
  color: #533f03;
}
vm-label[success], vm-label.vm-label--success {
  background-color: #4caf50;
  color: #fff;
}
vm-label[success][light], vm-label.vm-label--success.vm-label--light {
  background-color: #d4edda;
  color: #105242;
}
vm-label[success-low], vm-label.vm-label--success-low {
  background-color: #a6ce78;
  color: #fff;
}
vm-label[info], vm-label.vm-label--info {
  background-color: #25aae2;
  color: #fff;
}
vm-label[info][light], vm-label.vm-label--info.vm-label--light {
  background-color: #dfeffd;
  color: #25aae2;
}