.vm-search-menu {
  max-height: 215px;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
}
.vm-search-menu .vm-search-menu--item {
  background-color: #fff;
  border: none;
  border-radius: 3px;
  cursor: pointer;
  font-size: 16px;
  outline: none;
  padding: 10px;
  text-align: left;
}
.vm-search-menu .vm-search-menu--item:focus, .vm-search-menu .vm-search-menu--item:hover {
  background-color: #fafbfc;
}