vm-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: solid 1px #e7eaec;
  margin-bottom: 20px;
  padding: 10px 0;
}
vm-header.vm-header__no-border {
  border-bottom: none;
  padding: 10px 0 0 0;
}
vm-header .vm-header--title {
  display: flex;
  align-items: center;
  column-gap: 5px;
}
vm-header .vm-header--title h1 {
  font-size: 26px;
  font-weight: 700;
  margin: 0;
}