vm-dropdown {
  width: 100%;
  display: block;
  position: relative;
}
vm-dropdown.vm-dropdown__open .vm-dropdown--items {
  visibility: visible;
}
vm-dropdown .vm-dropdown--items {
  max-height: 241px;
  min-height: 41px;
  width: 100%;
  position: absolute;
  top: 39px;
  background-color: #fff;
  border: solid 1px #20a281;
  border-bottom-left-radius: 3px;
  border-bottom-right-radius: 3px;
  border-top: none;
  overflow-y: auto;
  visibility: hidden;
  z-index: 10;
}
vm-dropdown .vm-dropdown--items::-webkit-scrollbar {
  width: 13px;
}
vm-dropdown .vm-dropdown--items::-webkit-scrollbar-thumb {
  background: #c1c7d0;
  border-radius: 20px;
}
vm-dropdown .vm-dropdown--items::-webkit-scrollbar-track {
  background: #ebecf0;
}
vm-dropdown .vm-dropdown--items.vm-dropdown--items__searchable {
  top: 61px;
}
vm-dropdown .vm-dropdown--search,
vm-dropdown .vm-dropdown--selector {
  width: 100%;
  align-items: center;
  column-gap: 17px;
  background-color: #fff;
  font-size: 16px;
  outline: none !important;
}
vm-dropdown .vm-dropdown--search:hover,
vm-dropdown .vm-dropdown--selector:hover {
  cursor: pointer;
}
vm-dropdown .vm-dropdown--search {
  display: flex;
  border: solid 1px #20a281;
  border-top-left-radius: 3px;
  border-top-right-radius: 3px;
  padding: 10px 17px 10px 10px;
}
vm-dropdown .vm-dropdown--selector {
  height: 40px;
  display: grid;
  grid-template-columns: 1fr min-content;
  border: solid 1px #c1c7d0;
  border-radius: 3px;
  padding: 0 17px 0 13px;
}
vm-dropdown .vm-dropdown--selector:focus {
  border-color: #20a281;
}
vm-dropdown .vm-dropdown--selector:disabled {
  background-color: #fafbfc;
  color: #a6b0bc;
  cursor: not-allowed;
}
vm-dropdown .vm-dropdown--selector.vm-dropdown__open {
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
  border-color: #20a281;
}
vm-dropdown .vm-dropdown--selector .vm-dropdown--selector-text {
  overflow: hidden;
  text-align: left;
  text-overflow: ellipsis;
  white-space: nowrap;
}
vm-dropdown .vm-dropdown--selector .vm-dropdown--selector-text.vm-dropdown--placeholder {
  color: #a6b0bc;
}

.has-error > vm-dropdown .vm-dropdown--items,
.has-error > vm-dropdown .vm-dropdown--search,
.has-error > vm-dropdown .vm-dropdown--selector:not(:disabled) {
  border-color: #d61100 !important;
}