vm-search {
  height: 40px;
  width: 100%;
  display: flex;
  align-items: center;
  column-gap: 10px;
  background-color: #fff;
  border: solid 1px #c1c7d0;
  border-radius: 5px;
  font-size: 16px;
  padding: 0 10px;
}
vm-search:focus-within {
  border-color: #20a281;
}
vm-search.vm-search__disabled, vm-search.vm-search__disabled .vm-search--input {
  background-color: #fafbfc;
  cursor: not-allowed;
}
vm-search .vm-search--icon {
  position: relative;
  top: 1px;
}
vm-search .vm-search--input {
  height: 100%;
  width: 100%;
  border: none;
  outline: none;
  padding: 0;
}
vm-search .vm-search--input::placeholder {
  color: #a6b0bc;
}