vm-tooltip {
  display: inline;
  position: relative;
}
vm-tooltip .tooltip__info,
vm-tooltip .tooltip__info::before {
  background-color: #fafbfc;
  border: 1px solid #c1c7d0;
  position: absolute;
}
vm-tooltip .tooltip__info {
  min-width: 80px;
  display: none;
  border-radius: 3px;
  box-shadow: 0px 1px 4px rgba(22, 45, 80, 0.2);
  font-size: 11px;
  font-weight: 400;
  padding: 8px;
  white-space: nowrap;
  z-index: 2;
}
vm-tooltip .tooltip__info:before {
  content: "";
  height: 8px;
  width: 8px;
  border-bottom: none;
  border-right: none;
}
vm-tooltip .tooltip__info.tooltip__info--bottom.tooltip__info--pin-left, vm-tooltip .tooltip__info.tooltip__info--top.tooltip__info--pin-left {
  left: calc(50% - 31px);
}
vm-tooltip .tooltip__info.tooltip__info--bottom.tooltip__info--pin-left:before, vm-tooltip .tooltip__info.tooltip__info--top.tooltip__info--pin-left:before {
  left: 26px;
}
vm-tooltip .tooltip__info.tooltip__info--bottom.tooltip__info--pin-right, vm-tooltip .tooltip__info.tooltip__info--top.tooltip__info--pin-right {
  right: calc(50% - 31px);
}
vm-tooltip .tooltip__info.tooltip__info--bottom.tooltip__info--pin-right:before, vm-tooltip .tooltip__info.tooltip__info--top.tooltip__info--pin-right:before {
  right: 26px;
}
vm-tooltip .tooltip__info.tooltip__info--bottom {
  top: calc(100% + 12px);
}
vm-tooltip .tooltip__info.tooltip__info--bottom:before {
  top: -5px;
  transform: rotate(45deg);
}
vm-tooltip .tooltip__info.tooltip__info--top {
  bottom: calc(100% + 12px);
}
vm-tooltip .tooltip__info.tooltip__info--top:before {
  bottom: -5px;
  transform: rotate(225deg);
}
vm-tooltip .tooltip__info.tooltip__info--left, vm-tooltip .tooltip__info.tooltip__info--right {
  top: calc(50% - 15px);
}
vm-tooltip .tooltip__info.tooltip__info--left:before, vm-tooltip .tooltip__info.tooltip__info--right:before {
  top: 10px;
}
vm-tooltip .tooltip__info.tooltip__info--left {
  right: calc(100% + 10px);
}
vm-tooltip .tooltip__info.tooltip__info--left:before {
  left: calc(100% - 3px);
  transform: rotate(135deg);
}
vm-tooltip .tooltip__info.tooltip__info--right {
  left: calc(100% + 10px);
}
vm-tooltip .tooltip__info.tooltip__info--right:before {
  right: calc(100% - 3px);
  transform: rotate(315deg);
}
vm-tooltip:hover .tooltip__info, vm-tooltip:focus-within .tooltip__info {
  display: block;
}