.vm-dialog {
  width: 600px;
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.15);
}
.vm-dialog.vm-dialog__lg {
  width: 900px;
}
.vm-dialog .vm-dialog--body {
  padding: 25px;
}
.vm-dialog .vm-dialog--footer,
.vm-dialog .vm-dialog--header {
  display: flex;
  align-items: center;
  padding: 0 25px;
}
.vm-dialog .vm-dialog--footer {
  height: 75px;
  column-gap: 15px;
  justify-content: flex-end;
  border-top: solid 1px #c1c7d0;
}
.vm-dialog .vm-dialog--header {
  height: 70px;
  justify-content: space-between;
  border-bottom: solid 1px #c1c7d0;
}
.vm-dialog .vm-dialog--header .vm-dialog--title {
  font-size: 18px;
  font-weight: 700;
}

vm-form-wizard .vm-form-wizard {
  width: 80vw;
}
vm-form-wizard .vm-form-wizard .vm-form-wizard--header h4,
vm-form-wizard .vm-form-wizard .vm-form-wizard--header h2 {
  margin-top: 4px;
  margin-bottom: 4px;
}
vm-form-wizard .vm-form-wizard .vm-form-wizard-content {
  display: flex;
}
vm-form-wizard .vm-form-wizard .vm-form-wizard--nav {
  border: solid 1px #c1c7d0;
  border-top: 0;
  border-bottom: 0;
  border-left: 0;
  padding-left: 0;
  padding-right: 0;
  display: flex;
}
vm-form-wizard .vm-form-wizard .vm-form-wizard--nav [slot=nav] {
  min-width: 240px;
  width: 100%;
}
vm-form-wizard .vm-form-wizard .vm-form-wizard--body {
  padding-left: 12.5px;
  display: flex;
  min-width: 0;
  width: 100%;
}
vm-form-wizard .vm-form-wizard .vm-form-wizard--body [slot=body] {
  width: 100%;
}