.vm-dropdown-item {
  min-height: 40px;
  width: 100%;
  display: flex;
  align-items: center;
  background-color: #fff;
  border: none;
  border-top: solid 1px #eff2f7;
  font-size: 16px;
  outline: none !important;
  padding-left: 13px;
  text-align: left;
}
.vm-dropdown-item:hover, .vm-dropdown-item:focus {
  background-color: #fafbfc;
  cursor: pointer;
}
.vm-dropdown-item:disabled {
  opacity: 0.7;
}
.vm-dropdown-item.vm-dropdown-item__selected {
  background-color: #eff2f7;
  font-weight: 700;
}