@import url("https://fonts.googleapis.com/css?family=Lato");
@import url("https://fonts.googleapis.com/css?family=Lato:400italic");
@import url("https://cdn.jsdelivr.net/npm/bootstrap-reboot@4.3.1/dist/reboot.css");
* {
  font-family: "Lato", sans-serif;
}

body {
  color: #172b4a;
  margin: 0;
  font-size: 13px;
}

ux-dialog-container.active {
  background-color: rgba(60, 60, 60, 0.4);
}

.app {
  min-height: 100vh;
  display: grid;
  grid-template-columns: 220px 1fr;
}
.app .app--content {
  display: flex;
  flex-direction: column;
  padding: 20px;
}
.app .app--nav {
  background-color: #172b4a;
}
.app .app--nav .app--nav-item {
  height: 60px;
  display: flex;
  align-items: center;
  color: #c1c7d0;
  font-size: 14px;
  outline: none;
  padding: 0 8px 0 15px;
  text-decoration: none;
}
.app .app--nav .app--nav-item:focus, .app .app--nav .app--nav-item:hover {
  background-color: #2e405c;
  color: #fff;
}
.app .app--nav .app--nav-item.app--nav-item__active {
  background-color: #45556e;
  color: #fff;
}

.form-group {
  margin-bottom: 15px;
}
.form-group > label {
  font-weight: 700;
  margin-bottom: 5px;
}

.m-b {
  margin-bottom: 15px;
}

.sr-only {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}