@use "../../styles/variables" as v;

vm-badge {
    height: 20px;
    width: min-content;
    display: flex;
    align-items: center;
    border-radius: 50px;
    font-size: 12px;
    font-weight: 700;
    line-height: 14px;
    padding: 0 7px 0 6px;
}

vm-badge[danger],
.vm-badge__danger {
    background-color: v.$danger;
    color: v.$white;
}

vm-badge[primary],
.vm-badge__primary {
    background-color: v.$purple;
    color: v.$white;
}

vm-badge[secondary],
.vm-badge__secondary {
    background-color: v.$N30;
}
