vm-breadcrumbs {
  display: flex;
  column-gap: 13px;
  align-items: center;
  font-size: 16px;
  line-height: 24px;
}
vm-breadcrumbs .vm-breadcrumbs--breadcrumb {
  color: #20a281 !important;
  font-weight: 700;
  outline: none;
  text-decoration: none;
}
vm-breadcrumbs .vm-breadcrumbs--breadcrumb:hover, vm-breadcrumbs .vm-breadcrumbs--breadcrumb:focus {
  text-decoration: underline;
}
vm-breadcrumbs .vm-breadcrumbs--breadcrumb-divider {
  height: 16px;
  width: 1px;
  background-color: #a6b0bc;
  transform: rotate(22deg);
}